import { useState } from "react"
import Link from "next/link"
import { withStyles, createStyles } from "@material-ui/core"
import {
  AppBar,
  Toolbar,
  Drawer,
  IconButton,
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText
} from "@material-ui/core"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

const styles = theme =>
  createStyles({
    root: {
      margin: theme.spacing.unit * 2,
      flexGrow: 1,
      [theme.breakpoints.down("xs")]: {
        margin: theme.spacing.unit
      }
    },
    appBar: {
      backgroundColor: "inherit",
      padding: "0px",
      margin: "0px"
    },
    link: {
      color: theme.palette.primary.dark,
      textDecoration: "none"
    },
    title: {
      flexGrow: 1
    },
    menuButton: {
      marginLeft: -12,
      marginRight: 20
    },
    drawerList: {
      width: 250
    },
    menuIcon: {
      color: theme.palette.secondary.main
    }
  })

const Header = ({ classes }) => {
  const [showDrawer, setShowDrawer] = useState(false)
  return (
    <header className={classes.root}>
      <AppBar position="static" className={classes.appBar} elevation={0}>
        <Toolbar>
          <IconButton
            className={classes.menuButton}
            aria-label="Menu"
            onClick={() => setShowDrawer(!showDrawer)}
          >
            <FontAwesomeIcon icon="bars" className={classes.menuIcon} />
          </IconButton>
          <Typography variant="h1" className={classes.title}>
            <Link href="/">
              <a className={classes.link}>Cards of Crypto.</a>
            </Link>
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer open={showDrawer} onClose={() => setShowDrawer(false)}>
        <div className={classes.drawerList} onClick={() => setShowDrawer(false)}>
          <List>
            <DrawerItem link="/" text="Home" icon="home" />
            <DrawerItem link="/create" text="Create a card" icon="edit" />
            <DrawerItem link="/questions" text="Questions" icon="question" />
            <DrawerItemExternalLink
              link="https://broadhaven.tech"
              text="About Us"
              icon="user-tie"
              className={classes.link}
            />
            <DrawerItemExternalLink
              link="https://medium.com/@broadhaven.tech"
              text="Medium"
              icon={["fab", "medium"]}
              className={classes.link}
            />
            <DrawerItemExternalLink
              link="https://twitter.com/broadhaventech"
              text="Twitter"
              icon={["fab", "twitter"]}
              className={classes.link}
            />
          </List>
        </div>
      </Drawer>
    </header>
  )
}

const DrawerItem = ({ link, text, icon }) => (
  <Link href={link} key={text}>
    <ListItem button key={text}>
      <ListItemIcon>
        <FontAwesomeIcon icon={icon} />
      </ListItemIcon>
      <ListItemText primary={text} />
    </ListItem>
  </Link>
)

const DrawerItemExternalLink = ({ link, text, icon, className }) => (
  <a href={link} className={className} target="_blank" rel="noopener noreferrer" href={link}>
    <ListItem button key={text}>
      <ListItemIcon>
        <FontAwesomeIcon icon={icon} />
      </ListItemIcon>
      <ListItemText primary={text} />
    </ListItem>
  </a>
)

export default withStyles(styles)(Header)
