import { Component } from "react"
import getConfig from "next/config"
import NewTabLink from "../common/new-tab-link"
import { createStyles, withStyles } from "@material-ui/core"
import { Modal, Paper, Typography, Button, Grid } from "@material-ui/core"

const {
  publicRuntimeConfig: { DISCLAIMER, PRIVACY_POLICY }
} = getConfig()

const styles = theme =>
  createStyles({
    root: {
      margin: "200px auto",
      maxWidth: "400px",
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing.unit * 4
    },
    controls: {
      marginTop: theme.spacing.unit * 3,
      textAlign: "center"
    }
  })

class Disclamier extends Component {
  state = { open: false }

  componentDidMount = () => {
    if (!window.localStorage.disclaimer) {
      this.setState({ open: true })
    }
  }

  onAgree = () => {
    window.localStorage.setItem("disclaimer", Date.now())
    this.setState({ open: false })
  }

  render = () => {
    const { open } = this.state
    const { classes } = this.props

    if (open) {
      return (
        <Modal
          aria-labelledby="disclaimer"
          aria-describedby="site disclaimer and privacy policy"
          open={open}
          onClose={() => this.setState({ open: false })}
        >
          <Paper className={classes.root}>
            <Typography gutterBottom>
              This site uses magic and cookies and from time to time we may save some data about
              your visit.
            </Typography>
            <Typography gutterBottom>
              You can read the details in our <NewTabLink href={DISCLAIMER} text="disclaimer" /> and{" "}
              <NewTabLink href={PRIVACY_POLICY} text="privacy policy" />.
            </Typography>
            <div className={classes.controls}>
              <Button variant="outlined" onClick={() => this.setState({ open: false })}>
                Decline
              </Button>
              &nbsp;&nbsp;&nbsp;
              <Button variant="outlined" color="primary" onClick={() => this.onAgree()}>
                Accept
              </Button>
            </div>
          </Paper>
        </Modal>
      )
    }
    return null
  }
}

export default withStyles(styles)(Disclamier)
