import PropTypes from "prop-types"
import { withStyles, createStyles } from "@material-ui/core"

const styles = theme =>
  createStyles({
    root: {
      textDecoration: "none",
      color: theme.palette.primary.main,
      "&:hover": {
        color: theme.palette.secondary.main,
        textDecoration: "underline"
      }
    }
  })

const NewTabLink = ({ href, children, text, classes, ...rest }) => {
  let inner = text
  if (!text) inner = children

  return (
    <a target="_blank" rel="noopener noreferrer" href={href} className={classes.root} {...rest}>
      {inner}
    </a>
  )
}

NewTabLink.propTypes = {
  href: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(NewTabLink)
